<template>
  <div class="container">
    <h2 class="mb-5">Кабинеты</h2>

    <form action="" v-if="isAuth" class="mb-3 form">
      <label>
        Номер кабинета
        <input class="form-control" type="text" v-model="cabinet.number">
      </label>
      <br>
      <label>
        Этаж
        <input class="form-control" type="text" v-model="cabinet.floor">
      </label>
      <br>
      <label>
        Отделение
        <input class="form-control" type="text" v-model="cabinet.department">
      </label>
      <div class="buttons">
        <button
          @click.prevent="addCabinet"
          v-if="!isEdit"
          class="btn btn-success me-1"
        >Добавить
        </button>
        <button
          @click.prevent="saveCabinet"
          class="btn btn-success"
          v-if="isEdit"
        >Сохранить
        </button>
        <button
          class="btn btn-secondary"
          @click.prevent="clearForm"
        >Очистить/Отменить
        </button>
      </div>

    </form>

    <app-cabinets-list
      :cabinets="cabinets"
      :is-edit="isEdit"
      @deleteCabinet="deleteCabinet"
      @editCabinet="editCabinet"
    ></app-cabinets-list>

  </div>
</template>

<script>
import Rest, { authHeaders } from '../rest'
import AppCabinetsList from '@/components/AppCabinetsList'

export default {
  data () {
    return {
      cabinet: {
        number: null,
        floor: null,
        department: ''
      },
      currentCabinet: null,
      cabinets: [],
      isEdit: false
    }
  },
  methods: {
    async loadCabinets () {
      try {
        const { data } = await Rest.get('/cabinets')
        this.cabinets = data
      } catch (e) {
        console.error(e.message)
      }
    },
    async addCabinet () {
      try {
        await Rest.post('/cabinets', {
          number: parseInt(this.cabinet.number),
          floor: parseInt(this.cabinet.floor),
          department: this.cabinet.department
        }, authHeaders())
        await this.loadCabinets()
        this.clearForm()
      } catch (e) {
        this.$store.commit('setMessage', {
          type: 'alert-danger',
          value: e.message
        })
      }
    },
    async deleteCabinet (id) {
      try {
        await Rest.delete(`/cabinets/del?number=${id}`, authHeaders())
        await this.loadCabinets()
      } catch (e) {
        console.error(e.message)
      }
    },
    async editCabinet (id) {
      try {
        this.currentCabinet = id
        this.isEdit = true
        const { data } = await Rest.get(`/cabinets/${id}`)
        this.cabinet = {
          number: data.number,
          floor: data.floor,
          department: data.department
        }
        await this.loadCabinets()
      } catch (e) {
        console.error(e.message)
      }
    },
    async saveCabinet () {
      try {
        await Rest.put(`/cabinets/${this.currentCabinet}`, this.cabinet, authHeaders())
        await this.loadCabinets()
        this.clearForm()
        this.isEdit = false
      } catch (e) {
        console.error(e.message)
      }
    },
    clearForm () {
      this.cabinet = {
        number: '',
        floor: '',
        department: ''
      }
      this.isEdit = false
      this.currentCabinet = null
    }
  },
  components: { AppCabinetsList },
  async mounted () {
    await this.loadCabinets()
  },
  computed: {
    isAuth () {
      return this.$store.getters['auth/isAuthenticated']
    }
  }
}
</script>

<style scoped lang="stylus">
  form
    display flex
    align-items flex-end

    & > *
      &:not(:last-child)
        margin-right: 4px

</style>
