import Rest, { clearAuth, saveAuth } from '../rest'
import { error } from '../utils/error'

export default {
  namespaced: true,
  state () {
    return {
      sessionId: localStorage.getItem('USER_SESSION'),
      token: localStorage.getItem('USER_TOKEN'),
      ipAddress: localStorage.getItem('USER_ADDRESS')
    }
  },
  mutations: {
    setToken (state, payload) {
      state.sessionId = payload.sessionId
      state.token = payload.token
      state.ipAddress = payload.ipAddress

      saveAuth(payload.sessionId, payload.token, payload.ipAddress)
    },
    logout (state) {
      state.sessionId = null
      state.token = null
      state.ipAddress = null

      clearAuth()
    }
  },
  actions: {
    async login ({ commit, dispatch }, payload) {
      try {
        const { data } = await Rest.post('/login', payload)
        commit('setToken', data)
        commit('clearMessage', null, { root: true })
      } catch (e) {
        dispatch('setMessage', {
          value: error(e.response.status),
          type: 'alert-danger'
        }, { root: true })
        throw new Error()
      }

      // commit('setToken', 'TEST TOKEN')
    }
  },
  getters: {
    token (state) {
      return state.token
    },
    isAuthenticated (_, getters) {
      return !!getters.token
    }
  }
}
