<template>
  <div class="header position-sticky top-0">
    <div class="container">
      <div class="d-flex justify-content-between align-items-center">
        <div class="logo">
          <router-link to="/home">Clinic</router-link>
        </div>
        <ul class="nav">
          <li class="nav-item">
            <router-link to="/cabinets">Кабинеты</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/computers">Компьютеры</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/printers">Принтеры</router-link>
          </li>
          <li class="nav-item" v-if="isAuth">
            <router-link to="/users">Пользователи</router-link>
          </li>

          <li class="nav-item" v-if="isAuth">
            <router-link to="/registration">Регистрация</router-link>
          </li>

          <li class="nav-item ms-3" v-if="!isAuth">
            <router-link to="/auth">Войти</router-link>
          </li>

          <li class="nav-item ms-3" v-if="isAuth">
            <a href="" @click.prevent="logout">Выйти</a>
          </li>
        </ul>
      </div>
    </div>
  </div>

</template>

<script>
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { computed } from 'vue'

export default {
  setup () {
    const router = useRouter()
    const store = useStore()
    const isAuth = computed(() => store.getters['auth/isAuthenticated'])
    return {
      logout: () => {
        store.commit('auth/logout')
        router.push('/auth')
      },
      isAuth
    }
  }
}

</script>

<style scoped lang="stylus">
  .header
    padding: 16px 0
    background: #0d6efd

  .logo
    font-weight: bold;
    font-size: 32px
    color: #fff

  .nav-item
    &:not(:last-child)
      margin-right: 12px
    a
      color: rgba(#fff, .8)
      text-decoration none
      &.active
      &:hover
        color: #fff
  .logo
    a
      color #fff
      text-decoration: none
</style>
