<template>
  <div class="container">
    <form
      action=""
      class="form-control mb-5 mt-5 w-50 p-3 m-auto"
      @submit.prevent="registration"
    >
      <h3 class="form-title">Регистрация нового админа</h3>
      <div class="mb-3 ">
        <label for="username" class="form-label">Имя админа</label>
        <input
          id="username"
          type="text"
          class="form-control form"
          v-model="reg.user"
        >
      </div>
      <div class="mb-3">
        <label for="password" class="form-label">Пароль</label>
        <input
          id="password"
          type="password"
          class="form-control"
          v-model="reg.password"
        >
      </div>

      <button
        type="submit"
        class="btn btn-dark">Зарегистрировать
      </button>
    </form>
  </div>

</template>

<script>
import Rest, { authHeaders } from '../rest'

export default {
  data () {
    return {
      reg: {
        user: '',
        password: ''
      }
    }
  },
  methods: {
    async registration () {
      try {
        await Rest.post('/registration', this.reg, authHeaders())
        this.$store.commit('setMessage', {
          type: 'alert-success',
          value: `Создан админ ${this.reg.user} с паролем ${this.reg.password}`
        })
        this.reg = {
          user: '',
          password: ''
        }
      } catch (e) {
        console.error(e.message)
      }
    }
  }
}

</script>

