<template>
  <div class="container">
    <ul class="nav">
      <li class="nav-item">
        <router-link class="btn btn-primary mb-2" to="/cabinets">Кабинеты</router-link>
      </li>
      <li class="nav-item">
        <router-link class="btn btn-primary mb-2" to="/computers">Компьютеры</router-link>
      </li>
      <li class="nav-item">
        <router-link class="btn btn-primary mb-2" to="/printers">Принтеры</router-link>
      </li>
      <li class="nav-item" v-if="isAuth">
        <router-link class="btn btn-primary mb-2" to="/users">Пользователи</router-link>
      </li>

      <li class="nav-item" v-if="isAuth">
        <router-link class="btn btn-primary mb-2" to="/registration">Регистрация</router-link>
      </li>

      <li class="nav-item" v-if="!isAuth">
        <router-link class="btn btn-dark" to="/auth">Войти</router-link>
      </li>

      <li class="nav-item mt-3" v-if="isAuth">
        <a href="" class="btn btn-dark" @click.prevent="logout">Выйти</a>
      </li>
    </ul>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { computed } from 'vue'

export default {
  setup () {
    const router = useRouter()
    const store = useStore()
    const isAuth = computed(() => store.getters['auth/isAuthenticated'])
    return {
      logout: () => {
        store.commit('auth/logout')
        router.push('/auth')
      },
      isAuth
    }
  }
}
</script>

<style scoped lang="stylus">
  .nav
    display flex
    flex-direction column

</style>
