<template>
  <div v-if="cabinets.length !== 0">
    <table>
      <tr>
        <th>Номер кабинета</th>
        <th>Этаж</th>
        <th>Отделение</th>
      </tr>
      <tr v-for="cabinet in cabinets" :key="cabinet.number">
        <td>
          <router-link :to="`/cabinet/${cabinet.number}`">{{cabinet.number}}</router-link>
        </td>
        <td>{{cabinet.floor}}</td>
        <td>{{cabinet.department}}</td>
        <td v-if="isAuth">
          <button
            @click="$emit('editCabinet', cabinet.number)"
            class="btn btn-warning me-1"
          >Редактировать
          </button>
          <button
            @dblclick="$emit('deleteCabinet', cabinet.number)"
            class="btn btn-danger"
          >Удалить</button>
        </td>
      </tr>
    </table>
  </div>

  <div v-else>Список пуст</div>
</template>

<script>
export default {
  emits: ['editCabinet'],
  props: ['cabinets', 'isEdit'],
  computed: {
    isAuth () {
      return this.$store.getters['auth/isAuthenticated']
    }
  }
}
</script>

<style scoped lang="stylus">
  table
    width: 100%

  .buttons
    display: flex;

  .card
    display: flex;
    align-items: center
    flex-direction row
    justify-content: space-between;
</style>
