<template>
  <div class="container">
    <h3>Компьютеры в {{$route.params.id}} каб. Количество: {{getComputers.length}}</h3>

    <computer-form v-if="isAuth"/>

    <app-computers-list
      :computers="getComputers"
    ></app-computers-list>
  </div>

</template>

<script>
import AppComputersList from '@/components/AppComputersList'
import ComputerForm from '@/components/ComputerForm'
import { mapGetters } from 'vuex'
export default {
  methods: {
    async loadComputers () {
      await this.$store.dispatch('loadComputers', `/computers/cab?cabinet=${this.$route.params.id}`)
    }
  },
  computed: {
    ...mapGetters(['getComputers']),
    isAuth () {
      return this.$store.getters['auth/isAuthenticated']
    }
  },
  async mounted () {
    await this.loadComputers()
  },
  components: { AppComputersList, ComputerForm }
}
</script>

