<template>
  <form class="card w-25 m-auto p-3 mt-2" @submit.prevent="onSubmit">
    <b class="mb-2">Войти в систему</b>
    <div class="mb-2">
      <label for="email" class="form-label">Логин</label>
      <input type="text" :class="['form-control', {'is-invalid': eError}]" @blur="eBlur" id="email" v-model="email">
      <small v-if="eError">{{eError}}</small>
    </div>
    <div class="mb-3">
      <label for="password" class="form-label">Пароль</label>
      <input type="password" :class="['form-control', {'is-invalid': pError}]" @blur="pBlur" id="password"
             v-model="password">
      <small v-if="pError">{{pError}}</small>
    </div>
    <button class="btn btn-primary" type="submit" :disabled="isSubmitting">Войти</button>
  </form>
</template>

<script>
import { useLoginForm } from '../use/login-form'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { error } from '../utils/error'

export default {
  setup () {
    const route = useRoute()
    const store = useStore()
    if (route.query.message) {
      store.dispatch('setMessage', {
        value: error(route.query.message),
        type: 'alert-warning'
      })
    }
    return { ...useLoginForm() }
  }
}
</script>

